<template>
    <div v-if="showItem" @click="hideItem()" class="item">{{Itemname}}</div>
</template>

<script>
export default{
    props: {
        Itemname: {
            default: 'Valeur par defaut (Item)'
        }
    },
    data: () =>{
        return{
            showItem: true
        }
    },
    methods: {
        hideItem(){
            console.log(('hideItem'));
            this.showItem = false
        }
    }
    
}
</script>

<style>
.item{
    object-fit: cover;
    position: relative;
    text-align: center;
    border-radius: 50px;
    background-color: #393E46;
    
    margin-top: 20px;
    padding: 40px 40px;
    transition: 0.2s;
    cursor: pointer;
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 40px;
  
    color: #ffffff;
}
.item:hover{
    background-color: #00ADB5;
    color: white;
}
.item:active{
    transition: 0.1s;
    transform: scale(0.9);
}
</style>